<template>
  <div>
    <div class="md:w-2/3 md:mx-auto mx-2">
      <div
        v-for="(q, i) in allquestions"
        :key="i"
        class="bg-white mx-auto p-5 my-3 rounded-md"
      >
        <p v-html="q.question"></p>
        <div class="mt-3">
          <div
            v-html="q.a"
            v-if="q.a"
            @click="allquestions[i].selected = q.a"
            :class="`my-3 p-3 rounded  ${
              q.a == q.selected
                ? 'border-2 border-green-500 bg-green-300'
                : 'bg-gray-200'
            }`"
          ></div>
          <div
            v-html="q.b"
            v-if="q.b"
            @click="allquestions[i].selected = q.b"
            :class="`my-3 p-3 rounded  ${
              q.b == q.selected
                ? 'border-2 border-green-500 bg-green-300'
                : 'bg-gray-200'
            }`"
          ></div>
          <div
            v-html="q.c"
            v-if="q.c"
            @click="allquestions[i].selected = q.c"
            :class="`my-3 p-3 rounded  ${
              q.c == q.selected
                ? 'border-2 border-green-500 bg-green-300'
                : 'bg-gray-200'
            }`"
          ></div>
          <div
            v-html="q.d"
            v-if="q.d"
            @click="allquestions[i].selected = q.d"
            :class="`my-3 p-3 rounded  ${
              q.d == q.selected
                ? 'border-2 border-green-500 bg-green-300'
                : 'bg-gray-200'
            }`"
          ></div>
        </div>
      </div>
      <div class="text-center">
        <button
          @click="getResult"
          :disabled="loading"
          class="
            rounded
            py-3
            px-4
            shadow
            hover:shadow-lg
            bg-gradient-to-t
            from-green-500
            to-green-400
            mx-auto
            font-semibold
            text-white
            outline-none
          "
        >
          {{ loading ? "Loading..." : "Submit" }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Question",
  props: {
    allquestions: {
      type: Array,
    },
    inv: {
      type: String,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    testQ() {
      console.log("hi i am there");
    },
    getResult() {
      let score = this.allquestions.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 10;
        }
        return a;
      }, 0);

      this.$emit("result", score);
    },
  },
  watch: {
    loading: (n, o) => (n != o ? n : o),
  },
};
</script>

